/* 12. Members
/* ---------------------------------------------------------- */

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select {
    height: 44px;
    background: #fff;
    border-radius: 5px;
    border: none;
    border: solid 1px var(--lightgrey);
    color: inherit;
    display: block;
    outline: 0;
    padding: 0 0.6em;
    text-decoration: none;
    width: 100%;
}

input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="search"]:focus,
input[type="url"]:focus,
select:focus,
textarea:focus {
    border-color: var(--blue);
}

.site-signup {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.signup-form {
    width: 100%;
    max-width: 500px;
    margin: 0 auto 10vw;
    text-align: center;
}

.signup-title {
    margin: 0 0 0.1em;
    font-size: 4.2rem;
}

.signup-form p {
    color: var(--midgrey);
    font-size: 1.3em;
}

.signup-form p small {
    display: inline-block;
    margin: 15px 0 0 0;
    font-size: 1.4rem;
}

.signup-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.signup-box .button {
    margin-left: 10px;
}

@media (max-width: 600px) {
    .signup-box {
        flex-direction: column;
    }
    .signup-box .button {
        width: 100%;
        margin: 15px 0 0 0;
    }
}

