.checkout-form {
    width: 100%;
    max-width: 700px;
    margin: 0 auto 5vw;
    text-align: center;
}

.checkout-title {
    margin: 0 auto 10px;
    font-size: 4rem;
    font-weight: 700;
    letter-spacing: -0.6px;
}

.checkout-form p {
    max-width: 500px;
    margin: 0 auto 60px;
    color: var(--midgrey);
    font-size: 1.3em;
}

.checkout-box {
    display: flex;
    align-items: stretch;
    justify-content: center;
}

.checkout-plan {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    padding: 30px;
    border: 1px solid #c7d5d8;
    text-align: left;
    border-radius: 8px;
    color: #121519;
    background: #fff;
}

.checkout-plan + .checkout-plan {
    margin-left: 30px;
}

.checkout-plan-header {
    padding: 0 0 50px;
    color: var(--darkgrey);
    text-align: left;

    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid #c7d5d8;
}

.checkout-plan-header h3 {
    margin: 0 0 25px 0;
    color: var(--color-primary);
    font-size: 1.8rem;
    line-height: 1.15em;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

.checkout-plan-header span {
    font-size: 2.6rem;
    font-weight: 300;
}

.checkout-plan-header strong {
    color: var(--darkgrey);
    font-size: 4.5rem;
    font-weight: 500;
}

.checkout-plan-content {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.checkout-plan-content ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.checkout-plan-content li {
    position: relative;
    margin: 0;
    padding: 0 0 15px 0px;
    line-height: 1.4em;
}

.checkout-plan-content .button {
    margin: 20px 0 0 0;
}

.checkout-plan-notes {
    max-width: 550px;
    margin: 0 auto 5vw;
    text-align: center;
}

@media (max-width: 740px) {
    .checkout-title {
        margin-top: 50px;
    }
    .checkout-box {
        flex-direction: column;
    }
    .checkout-plan + .checkout-plan {
        margin: 5vw 0 0 0;
    }
}

form[data-members-form] .button-loader,
form[data-members-form] .message-success,
form[data-members-form] .message-error {
    display: none;
}

.message-success svg,
.message-error svg {
    position: relative;
    top: -1px;
    margin-right: 5px;
    height: 15px;
    width: 15px;
}
.message-success svg {
    fill: #fff;
}
.message-error svg {
    fill: var(--red);
}

form[data-members-form].success .message-success,
form[data-members-form].error .message-error {
    position: fixed;
    top: 84px;
    left: 20px;
    right: 20px;
    z-index: 9999;
    margin: 0 auto;
    padding: 10px 0;
    color: #fff;
    font-size: 1.4rem;
    line-height: 1.5em;
    font-weight: 500;
    text-align: center;
    background: color(var(--green) l(-5%));
    border-radius: 5px;
}
form[data-members-form].success .message-success {
    background: color(var(--green) l(-5%));
}
form[data-members-form].error .message-error {
    color: var(--red);
    background: #fff;
    box-shadow: var(--red) 0 0 0 1px;
}

form[data-members-form] .button {
    position: relative;
}

/*-- Loading --*/

/* Hide button text */
form[data-members-form].loading .button-content {
    visibility: hidden;
}

/* Show loading spinner */
form[data-members-form].loading .button-loader {
    display: inline-block;
    position: absolute;
    left: 50%;
    height: 31px;
    margin-left: -19px;
    transform: scale(0.7);
}

.button-loader svg path,
.button-loader svg rect{
  fill: #fff;
}

/*-- Show success message on success --*/
form[data-members-form].success .message-success {
    display: block;
}

/*-- Show error message on error --*/
form[data-members-form].error .message-error {
    display: block;
}

.site-account {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5vw;
}

.account-box {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}

.account-box-title {
    font-size: 3rem;
}

.header-cta {
    margin-left: 15px;
}

.post-card-header-tags {
    display: flex;
    align-items: center;
}

.post-visibility {
    display: inline-block;
    margin: 0 8px 1px 0;
    padding: 1px 5px;
    font-size: 1.2rem;
    line-height: 1.4em;
    border: var(--lightgrey) 1px solid;
    color: var(--midgrey);
    border-radius: 4px;
}

.post-visibility-members,
.post-visibility-paid {
    display: none;
}

.post-upgrade-cta {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.post-upgrade-cta-content {
    max-width: 840px;
    width: 90vw;
    margin: 0 5vw;
    padding: 40px 30px;
    text-align: center;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    color: #fff;
    background: color(var(--blue) l(-5%));
    border-radius: 5px;
}

.post-upgrade-cta-content h2 {
    margin: 0;
    color: #fff;
    font-weight: 600;
    font-size: 2.6rem;
    letter-spacing: -0.5px;
}

.post-upgrade-cta .button,
.post-upgrade-cta .button:hover {
    margin: 30px 0;
    color: var(--blue);
    background: #fff;
    box-shadow: none;
    text-decoration: none;
}

.post-upgrade-cta p {
    margin: 0;
}

.post-upgrade-cta a,
.post-upgrade-cta a:hover {
    color: #fff;
    text-decoration: underline;
    box-shadow: none !important;
}